*{
    box-sizing: border-box;
}


body{
    color: white;
    background-color: #282d35;
    font-family: 'Roboto', sans-serif;
    
}
.container {
    max-width: 400px;
    padding: 0;
    margin: auto;
    margin-top: 8px;
    margin-bottom: 8px;
    width: 40%;
    height: 100%;
    background-color: #21222A;
    border-radius: 10px;
    text-align: center;
}

h1{
    margin: 6px;
    color: white;
    text-align: center;
    padding: 16px;

}
img{
    border-radius: 10px 10px 0px 0px;
    width: 100%;

    margin: 0px;
}
section{
    background-color: white;
    margin: 16px;
    max-width: 400px;
    border-radius: 10px;
}
a {
    
    width: 50%;
    text-decoration: none;
    color: #21222A;
}
p{
    padding: 5px;
   
}

.title{
    margin: 0px;
    max-width: 400px;
    padding-left: 16px;
    text-align: left;
    font-weight: 700;
}

.text {
    max-width: 400px;
    text-align: initial;
    padding: 0px 16px 16px;
    font-weight: 400;
    font-size: 12px;
    
}

.Card--Footer {
    width: 100%;
    background-color: grey;
    border-radius: 0px 0px 10px 10px;
    display: flex;
}
.container--footer{
    margin: auto;
}